import React, { MutableRefObject } from 'react';
import './Receiver.css';

import Webcam from "react-webcam";

import { Box, Button, Container } from '@material-ui/core';

type ReceiverState = {
  videoConstraints?: MediaStreamConstraints["video"],
  currentFacingMode: CameraFacingModes,
}

enum CameraFacingModes {
  USER = 'user',
  ENVIRONMENT = 'environment'
}

class Receiver extends React.Component<{}, ReceiverState> {

  webcamRef?: MutableRefObject<Webcam>;

  constructor() {
    super({});

    const defaultFacingMode = CameraFacingModes.ENVIRONMENT;

    this.state = {
      currentFacingMode: defaultFacingMode,
      videoConstraints: {
        facingMode: defaultFacingMode
      },
    };

    this.toggleCamera = this.toggleCamera.bind(this);
  }

  toggleCamera() {
    const toggledFacingMode = (this.state.currentFacingMode == CameraFacingModes.USER ) ? CameraFacingModes.ENVIRONMENT : CameraFacingModes.USER;
    
    this.setState({
      videoConstraints: { facingMode: toggledFacingMode },
      currentFacingMode: toggledFacingMode,
    });
  }

  // receive() {
  //   if (this.webcamRef) {
  //     const imageSrc = this.webcamRef.current.getScreenshot();
  //     console.log('imageSrc', imageSrc);
  //   }
  // }

  render() {    
    return <Container maxWidth="sm">
      <h3>Receive</h3>
      <Box>
        <Webcam 
          videoConstraints={this.state.videoConstraints}
          ref={this.webcamRef}
          style={{width: '70vmin', height: '70vmin'}}
        />
      </Box>
      <Button variant="contained" color="primary" onClick={this.toggleCamera} >Toggle Camera</Button>
      {/* <Button variant="contained" color="primary" onClick={this.receive} >Receive</Button> */}
    </Container>;
  }
}

export default Receiver;